.container {
    display: flex;
    flex-direction: row;
    align-content: flex-end;
    justify-content: center;
    align-items: center;
    width: 90vw;
    margin-top: 80px;
    margin-left: 80px;

}

.image {
    border-radius: 10%;
    height: 80vh;
    cursor: pointer;
    flex: 0.5;
    margin: 10px;
    cursor: pointer;
    margin: 10px;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    position: relative;
    transition: flex 0.7s ease-in;
}

.image .active {
    width: 90vw;
    flex: 5;
    padding-left: 20px;
}

.notActive {
    width: 6vw;
    color: transparent;
}